.education-sections {
	display: flex;
	flex-direction: column;
	gap: 16px;
}

.education-section {
	position: relative;
	display: flex;
	flex-direction: column;
}

.education-section button {
	position: absolute;
	top: 0px;
	right: 0px;
}

.ed-cert {
	font-weight: bold;
}

.ed-name, .ed-location {
	font-style: italic;
}

.ed-description {
	font-size: 12px;
	white-space: pre-line;
}

.ed-from, .ed-to {
	font-size: 12px;
}